body {
    font-family: 'Open Sans', sans-serif;
    // font-size: 1.6rem;
    line-height: 1.7;
    font-weight: 400;
}

.heading-primary {
    margin-bottom: 3rem;
    & > * {
        display: block;
        font-weight: 400;
    }

    &--main {
        font-size: 9rem;
        text-transform: uppercase;
        letter-spacing: 2rem;
    }
    &--sub {
        font-size: 2rem;
        text-transform: uppercase;
        letter-spacing: 2rem;
    }
}

.heading-secondary {
    font-size: 4rem;
    font-weight: 600;
    letter-spacing: .2rem;
    color: transparent;
    text-transform: uppercase;
    background: linear-gradient(to right, $color-primary-light, $color-primary-dark);
    background-clip: text;
    -webkit-background-clip: text;
}

.heading-tertiary {
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 600;
    color: $color-grey-dark-2;
}

.paragraph {
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-grey-dark-2;
}